import React from 'react';
import { graphql, Link } from "gatsby";
import Layout from "../components/Layout";
import { SEO } from "../components/SEO";
import { GatsbyImage, getImage } from "gatsby-plugin-image";
import ContentfulRichTech from '../components/contentful-rich-text';
import Gitalk from 'gatsby-plugin-gitalk';
import '@suziwen/gitalk/dist/gitalk.css';

const ComponentName = ({ data, pageContext }) => {
  const { content, title } = data.blog;
  const image = getImage(data.blog.image);
  const { description } = data.contentfulBlogPostDescriptionTextNode;
  const { slug } = pageContext;
  ;

  return (
    <Layout>
      <GatsbyImage image={image} alt="" className="blog-img-header" />
      <SEO title={title} description={description} />
      <section className="blog-template">
        <div className="section-center">
          <article className="blog-content">
            
            <h1>{title}</h1>
            <ContentfulRichTech richText={content} />
          </article>
          <Gitalk options={{ id: slug, title: title, distractionFreeMode: true }}/>
          <Link to="/blog" className="btn center-btn">blog</Link>
        </div>
      </section>
    </Layout>
  );
};

export const query = graphql`
  query GetSingleBlog($slug: String) {
    blog: contentfulBlogPost(slug: { eq: $slug }) {
      content {
        raw
        references {
          ... on ContentfulAsset {
            contentful_id
            title
            description
            gatsbyImageData(layout: CONSTRAINED)
            __typename
          }
        }
      }
      title
      image {
        gatsbyImageData(layout: CONSTRAINED)
      }
    }
    contentfulBlogPostDescriptionTextNode {
      description
    }
  }
`;

export default ComponentName;
